.input {
outline: none;
border: none;
border-bottom:1px solid rgb(156, 156, 156);
background-color: white; 
width: 250px;
height: 30px;
font-size: 18px;
padding-left: 10px;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}